<template>
	<div>
		<p style="margin-top: -10px; color: #1890FF; text-decoration:underline; cursor:pointer" @click="showDrawer"> Register
			now! </p>
		<a-drawer title="注册一个新账号" :width="500" :visible="visible" :body-style="{ paddingBottom: '80px' }" @close="onClose">
			<a-form :form="form" layout="vertical" hide-required-mark>
				<a-row :gutter="16">
					<a-col :span="12">
						<a-form-item label="用户名*">
							<a-input v-decorator="[
                  'name',
                  {
                    rules: [{ required: true, message: 'User name can not be empty' }],
                  },
                ]"
							 placeholder="Please enter user name" />
						</a-form-item>
					</a-col>
				</a-row>
				<a-row :gutter="16">
					<a-col :span="12">
						<a-form-item label="密码*">
							<a-input v-decorator="[
							  'password',
							  { rules: [{ required: true, message: 'Password can not be empty' }] },
							]"
							 type="password" placeholder="Password">
								<a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
							</a-input>
						</a-form-item>
					</a-col>
				</a-row>
				<a-row :gutter="16">
					<a-col :span="12">
						<a-form-item label="性别">
							<a-select v-decorator="['gender']" placeholder="Please choose the gender">
								<a-select-option value="M">
									男性
								</a-select-option>
								<a-select-option value="F">
									女性
								</a-select-option>
							</a-select>
						</a-form-item>
					</a-col>
					<a-col :span="12">
						<a-form-item label="年龄段">
							<a-select v-decorator="['age']" placeholder="Please choose the age">
								<a-select-option :value=key v-for="(value, key) in ageList">
									{{value}}
								</a-select-option>
							</a-select>
						</a-form-item>
					</a-col>
				</a-row>
				<a-row :gutter="16">
					<a-col :span="12">
						<a-form-item label="职业">
							<a-select v-decorator="['occupation']" placeholder="Please choose the occupation">
								<a-select-option :value=key v-for="(value, key) in occupationList">
									{{value}}
								</a-select-option>
							</a-select>
						</a-form-item>
					</a-col>
					<a-col :span="12">
						<a-form-item label="邮政编码">
							<a-input v-decorator="['zipCode']" placeholder="Please enter user zip-code" />
						</a-form-item>
					</a-col>
				</a-row>
				<a-row :gutter="16">
					<a-col :span="12">
						<a-form-item label="头像">
							<a-upload name="file" list-type="picture-card" class="uploader" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadHeadPic'"
							 :before-upload="beforeUpload" @change="handleChange">
								<a-avatar v-if="imageUrl" :src="imageUrl" alt="avatar" :size="80"> </a-avatar>
								<!-- <img v-if="imageUrl" :src="imageUrl" alt="avatar" style="width: 128px; height: 128px;" /> -->
								<div v-else>
									<a-icon :type="loading ? 'loading' : 'plus'" />
									<div class="ant-upload-text">
										上传
									</div>
								</div>
							</a-upload>
						</a-form-item>
					</a-col>
				</a-row>
			</a-form>
			<div :style="{
			  position: 'absolute',
			  right: 0,
			  bottom: 0,
			  width: '100%',
			  borderTop: '1px solid #e9e9e9',
			  padding: '10px 16px',
			  background: '#fff',
			  textAlign: 'right',
			  zIndex: 1,
			}">
				<a-button :style="{ marginRight: '8px' }" @click="onClose">
					Cancel
				</a-button>
				<a-button type="primary" @click="handleSubmit">
					Submit
				</a-button>
			</div>
		</a-drawer>
	</div>
</template>
<script>
	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	};
	export default {
		async created() {
			this.getAllAge();
			this.getAllOccupation();
		},
		data() {
			return {
				form: this.$form.createForm(this),
				visible: false,
				ageList: null,
				occupationList: null,
				loading: false,
				imageUrl: null,
				user: {
					"name": "undefine",
					"password": "undefine",
					"gender": "undefine",
					"age": null,
					"occupation": null,
					"zipCode": "undefine",
					"headPic": "default.jpg"
				}
			};
		},
		methods: {
			async getAllAge() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'allAge');
				this.ageList = data.data;
			},
			async getAllOccupation() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'allOccupation');
				this.occupationList = data.data;
			},
			async userRegister() {
				let {
					data
				} = await this.axios.post(this.$store.state.requestPath + 'register/', this.user);
				if (data.code == 0) {
					this.onClose();
					this.$message.success(' (｡･∀･)ﾉﾞ 注册成功~ ', 1);
				} else {
					this.$message.error(' (￣△￣；) 出错啦~ ', 2);
				}
			},
			showDrawer() {
				this.visible = true;
			},
			onClose() {
				this.visible = false;
				this.imageUrl = null;
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.loading = true;
					return;
				}
				if (info.file.status === 'done') {
					// 从前端获取上传的 img-url
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.loading = false;
					});
					this.user.headPic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
			async handleSubmit(e) {
				e.preventDefault();
				this.form.validateFields((err, values) => {
					if (!err) {
						this.user.name = values.name;
						this.user.password = values.password;
						this.user.gender = values.gender;
						this.user.age = values.age;
						this.user.occupation = values.occupation;
						this.user.zipCode = values.zipCode;
						this.userRegister();
					}
				});
			},
		},
	};
</script>


<style>
	.uploader>.ant-upload {
		border-radius: 50%;
	}

	.ant-upload-select-picture-card i {
		font-size: 32px;
		color: #999;
	}

	.ant-upload-select-picture-card .ant-upload-text {
		margin-top: 8px;
		color: #666;
	}
</style>
